

header {
  padding: 20px;
  background-color:rgb(61, 133, 240);
  color:white;
}

header h1 {
  display: inline-block;
  font-family: "Kaushan Script";
  font-size: 48px;
  margin-left: 20px; 
  margin-right: 20px; 
}

header nav {
  padding-top: 10px;
  margin-right: 20px;
  float: right;
  font-family: "Ubuntu";
  font-size: 20px;
}

.nav-tabs {
  list-style-type: none;
}

.nav-item {
  display: inline-block;
  margin-left: 10px; 
  margin-right: 10px; 
  color: white;
}

.active {
  background-color: #d5ddfd;
}

a.nav-link {
  color: white;
}

header a:hover {
  color: rgb(1, 11, 26);
}

@media screen and (max-width: 1100px) {
  #about-image {
      max-height: 60px;
  }
  body {
      padding: 10px;
  } 

}

@media screen and (max-width: 1000px) {
  #about-image {
      max-height: 50px;
  }
  #feature {
      padding: 10px;
  }
  #portfolio-card {
      min-width: 300px;
  }
  #button {
      max-width: 200px;
  }
  #card-image {
      height: 40vh;
  }
}     

@media screen and (max-width: 945px) {
  header {
      text-align: center;
      margin:0;
  }    
  header nav {
      text-align: center;
      float: none;
      margin-right:auto;
      margin-left:auto;
      width:510px;
  }
  main {
      margin-top: 170px;
    }  
}

@media screen and (max-width: 760px) {
  header nav {
      font-size: 18px;
  }    
}     

@media screen and (max-width: 600px) {
  body {
      padding-left: 0;
      padding-right: 0;
  }
}



